import React from 'react'
import { Link } from 'react-router-dom'

type CartEmptyProps = {
     back: string[];
     title: number;
}
const CartEmpty: React.FC<CartEmptyProps> = ({ back, title }) => {
     return (
          <div className='cart--empty'>
               <h2>  </h2>
               <p>
                         
               </p>
               <Link to={'/menu'} className="cart--empty__back">
                      {back[title]} 
               </Link>
          </div>
     )
}

export default CartEmpty