import React from 'react'
type FormProps = {
     type: string;
}
const Form: React.FC<FormProps> = ({ type }) => {
     if (type == 'login') {
          return (
               <form>
                    <input type='email' placeholder=' email...' />
                    <input type='password' placeholder=' ...' />
                    <button></button>
               </form>
          )
     }
     else {
          return (
               <form>
                    <input placeholder=' ...' />
                    <input placeholder=' ...' />
                    <input type='email' placeholder=' email...' />
                    <input type='password' placeholder=' ...' />
                    <input type='password' placeholder=' ...' />
                    <button></button>
               </form>
          )
     }

}

export default Form